@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Montserrat", sans-serif;
    line-height: 1.5;
    color: var(--text-body);
    background: var(--bg-body);
    transition: background 500ms ease-in-out, color 1000ms ease-in-out;
}

/* Header */

.header-background {
    background: var(--bg-intro);
}

/* Top-bar */

.top-bar-container {
    height: 70px;
    width: 100%;
}

.top-bar {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: .5em 1.5em;
    list-style-type: none;
    transition: top .3s ease;
    z-index: 999;
}

.li-item {
    width: 6rem;
    height: 4.5rem;
    box-shadow: 4px 0 10px -3px #010101;
    background: var(--bg-topbar);
    color: var(--text-head);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.first-item {
    border-bottom-left-radius: 40%;
}

.start-app {
    background: var(--app);
}

/* Theme Changer */

.theme-icon {
    font-size: 50px;
}

/* Intro Panel */

.intro-panel {
    height: 20vh;
    margin: 5rem 3rem 0 5rem;
    text-align: center;
}

.panel-heading {
    font-weight: 800;
    margin-bottom: 3rem;
}

.panel-text {
    font-weight: 600;
}

/* App */

.app-container {
    color: var(--text-body);
    background: var(--bg-body);
}

/* Form */

.form-container {
    position: fixed;
    top: 150px;
    left: calc(50% - 230px);
    width: 300px;
    padding: 10px 10px 20px;
    text-align: center;
    color: var(--text-body);
    background: var(--bg-form);
    border-radius: 20px;
    z-index: 999;
}

.form-label {
    display: block;
    margin-top: 7px;
    padding: 3px;
    font-weight: 600;
}

.form-input {
    display: block;
    margin: 5px auto 10px;
    width: 90%;
    border: 0;
    padding: 3px;
    background: var(--bg-form);
    color: var(--text-body);
    border-bottom: 1px solid var(--text-body);
}

.form-btn {
    display: block;
    margin: 10px auto 5px;
    box-shadow: 0 3px 2px 0 rgba(0,0,0,0.1);
    border-radius: 5px;
    border: 0;
    height: 2.24rem;
    padding: 0 30px;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--text-body);
    background: var(--bg-body);
    cursor: pointer;
}

.close-form {
    position: absolute;
    top: -2px;
    right: -1px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.error-msg {
    font-weight: 700;
}

.hidden {
    display: none;
}

/* List */

.list-container {
    margin: 10px;
    border-radius: 30px;
    background: var(--bg-list);
}

.list-expired .card-container {
    background: var(--bg-expired);
}

.list-done .card-container {
    background: var(--bg-done);
}

.list-heading {
    text-align: center;
    margin-top: 0.9rem;
    margin-bottom: 1.25rem;
    padding: 5px;
    font-weight: 700;
}

.list-label {
    display: inline-block;
    vertical-align: middle;
    margin-top: 3px;
}

.del-list {
    position: absolute;
    margin-left: 10px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}

/* Card */

.wrapper {
    display: grid;
}

.card {
    display: flex;
    flex-wrap: wrap;
}

.card-container {
    align-items: center;
    padding-left: 5px;
    position: relative;
    border-radius: 10px;
    box-shadow: 0px 5px 10px rgb(71, 71, 71);
    display: grid;
    width: 350px;
    height: 120px;
    color: var(--text-body);
    background: var(--bg-card);
    margin-left: 12px;
    margin-right: 12px;
    margin-bottom: 2rem;
    grid-template-areas: 
        "name-heading name name"
        "date-heading date date"
        "countdown-h countdown countdown";
}

.card-delete-btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    padding-top: 5px;
    color: var(--del-btn);
    cursor: pointer;
}

.card-complete-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 30px;
    height: 30px;
    padding-bottom: 5px;
    color: var(--com-btn);
    cursor: pointer;
}

.card-delete-btn,
.card-complete-btn {
    padding-right: 5px;
}

.card-heading {
    font-weight: 600;
    padding: 5px;
}

.card-content {
    width: 100%;
    padding: 5px;
}

.card-name {
    grid-area: name;
    overflow: hidden;
    white-space: nowrap;
    overflow-wrap: break-word;
    word-wrap: break-word;
    text-overflow: ellipsis;
    max-width: 200px;
}

.card-date {
    grid-area: date;
}

.show-full-name {
    position: absolute;
    top: 35px;
    left: 15px;
    max-width: calc(100% - 30px);
    width: auto;
    height: fit-content;
    word-wrap: break-word;
    color: var(--full-name-body);
    background: var(--bg-full-name);
    border: 3px solid var(--bg-full-name);
    border-radius: 9px;
    width: minmax(0, 300px);
    z-index: 999;
}

.colon {
    display: inline-block;
    margin: 0 2px;
}

/* How To Use */

.container {
    margin: 2rem 5rem 2rem 5rem;
    align-items: center;
}

.how-to-use--container {
    margin-bottom: 5rem;
}

.heading {
    font-weight: 700;
    text-align: center;
    padding-top: 2rem;
    margin-bottom: 3rem;
}

.text-style {
    margin-bottom: 1rem;
}

/* Footer */

.footer-container {
    padding: 15px 15px 5px 15px;
    color: var(--text-body);
    background: var(--bg-intro);
    text-align: center;
}

.footer-heading {
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
}

.social-links {
    display: flex;
    justify-content: center;
    list-style-type: none;
}

.social-item {
    padding: 1rem;
    font-size: 50px;
}

.link-item {
    color: var(--text-body);
}

.copyright {
    font-size: 12px;
    text-align: center;
}

/* Themes */

:root {
    --gray0: #f8f8f8;
    --gray1: #dbe1e8;
    --gray2: #b2becd;
    --gray3: #6c7983;
    --gray4: #454e56;
    --gray5: #2a2e35;
    --gray6: #12181b;
    --lightblue: lightblue;
    --lightgreen: rgb(132, 223, 132);
    --darkgreen: rgb(18, 90, 18);
    --bg-intro-color: #a1b4c5;
    --divide-line: #e0dede;
    --bg-quotes-light: #f1f1f1f1;
    --bg-quotes-dark: #3e4449;
    --btn-red-light: #ff0000;
    --btn-green-light: #1dca1d;
    --btn-red-dark: #8d1b1bd2;
    --btn-green-dark: #1f7a2e;
    --bg-form-btn-dark: rgba(0, 0, 0, 0.8);
    --bg-form-btn-light: rgba(0, 0, 0, 0.3);
    --bg-list-light: #e0dede;
    --bg-list-dark: var(--gray6);
    --bg-expired-light: #ff9999;
    --bg-expired-dark: #421818;
    --bg-done-light: #9df8a9;
    --bg-done-dark: #183b1d;
}

.light {
    --bg-intro: var(--bg-intro-color);
    --bg-body: var(--gray0);
    --bg-topbar: var(--lightblue);
    --bg-slideshow: var(--bg-quotes-light);
    --text-head: var(--gray0);
    --text-body: var(--gray6);
    --text-slideshow: var(--gray6);
    --app: var(--lightgreen);
    --border: var(--divide-line);
    --bg-card: var(--gray0);
    --bg-form: var(--gray2);
    --del-btn: var(--btn-red-light);
    --com-btn: var(--btn-green-light);
    --bg-hover-form-btn: var(--bg-form-btn-light);
    --bg-list: var(--bg-list-light);
    --bg-expired: var(--bg-expired-light);
    --bg-done: var(--bg-done-light);
    --bg-full-name: var(--gray0);
    --full-name-body: var(--gray6);
}

.dark {
    --bg-intro: var(--gray3);
    --bg-body: var(--gray5);
    --bg-topbar: var(--gray4);
    --bg-slideshow: var(--bg-quotes-dark);
    --text-head: var(--gray0);
    --text-body: var(--gray0);
    --text-slideshow: var(--gray0);
    --app: var(--darkgreen);
    --border: var(--divide-line);
    --bg-card: var(--gray3);
    --bg-form: var(--gray4);
    --del-btn: var(--btn-red-dark);
    --com-btn: var(--btn-green-dark);
    --bg-hover-form-btn: var(--bg-form-btn-dark);
    --bg-list: var(--bg-list-dark);
    --bg-expired: var(--bg-expired-dark);
    --bg-done: var(--bg-done-dark);
    --bg-full-name: var(--gray4);
    --full-name-body: var(--gray0);
}

/* Hover and Focus */

.form-btn:hover {
    box-shadow: 0 4px 17px rgba(0,0,0,0.2);
    background: var(--bg-hover-form-btn);
}

.card-delete-btn:hover,
.card-complete-btn:hover,
.close-form:hover,
.del-list:hover,
.li-link:hover,
.li-item:hover,
.link-item:hover {
    color: rgba(0, 0, 0, 0.5);
}

/* Media Queries */

@media(max-width: 767px) {
    .container {
        margin: 1rem .5rem 0 1rem;
    }

    .card-container {
        width: 330px;
    }

    .card-name {
        max-width: 180px;
    }

    .form-container {
        top: 100px;
        left: calc(50% - 165px);
    }

    .header-background {
        padding-bottom: 5px;
    }

    .intro-panel {
        height: fit-content;
        margin-top: 0;
    }

    .panel-heading {
        margin-bottom: 1rem;
    }

    .panel-text {
        margin-bottom: 10px;
    }

    .li-item {
        width: 4rem;
        height: 3.5rem;
    }
}